import React from 'react'
import Gallery from './../shared/gallery';
import PageTitle from './../shared/page-title';
import SectionTitle from './../shared/section-title';
import Paragraph from './../shared/paragraph';
import PageIntro from '../shared/page-intro';
import StoreButtons from './../shared/store-buttons';
import ArticleImages from './../shared/article-images';
import Comments from './../shared/comments';
import Link from './../shared/link';

import logo from './../img/nsf/logo.png';
import img1 from './../img/nsf/screenshots/gameplay.gif';
import img2 from './../img/nsf/screenshots/1.png';
import img3 from './../img/nsf/screenshots/2.png';
import img4 from './../img/nsf/screenshots/3.png';
import img5 from './../img/nsf/screenshots/4.png';
import img6 from './../img/nsf/screenshots/5.png';

export default class Page extends React.Component {
    render() {
        return (
            <div>
                <PageTitle text="Not So Flat"/>

                <PageIntro logo={logo}>
                    <Paragraph>A short experimental 2D puzzle platformer set in a 3D world.</Paragraph>
                    <Paragraph><strong>Release date:</strong> v1.0 - April 14, 2015; v1.1 - July 14, 2015</Paragraph>
                    <Paragraph><strong>Platforms:</strong> PC, Mac</Paragraph>
                    <Paragraph><Link to="/nsf-devblog">Devlog</Link></Paragraph>
                    <StoreButtons itch="http://0xc0dec.itch.io/notsoflat"/>
                    <ArticleImages images={[img1]}/>
                </PageIntro>

                <SectionTitle text="Screenshots"/>

                <Gallery
                    images={[
                        { src: img2 },
                        { src: img3 },
                        { src: img4 },
                        { src: img5 },
                        { src: img6 }
                    ]}
                />

                <Comments/>
            </div>
        );
    }
}


